import {AfterViewInit, Component, OnInit} from '@angular/core';
import {Course} from '../entity';
import {CourseService} from '../course.service';
import {ActivatedRoute} from '@angular/router';
import {catchError} from 'rxjs/operators';

@Component({
  selector: 'app-charts',
  templateUrl: './charts.component.html',
  styleUrls: ['./charts.component.css']
})
export class ChartsComponent implements OnInit, AfterViewInit {
  private courses: Course[];
  private newCourses: string[];
  private countNum: number[];

  constructor(
    private route: ActivatedRoute,
    private courseService: CourseService
  ) {
  }

  ngOnInit() {
    this.getCourses();
  }

  ngAfterViewInit(): void {
    const Highcharts = require('highcharts');
    require('highcharts/modules/exporting')(Highcharts);
    // 创建图表
    Highcharts.chart('container', {
      chart: {
        type: 'line'
      },
      title: {
        text: '各课程学生人数统计'
      },
      subtitle: {
        text: '来源： 学生选课情况'
      },
      xAxis: {
        categories: ['大学英语3', '大学体育3', 'JavaWeb程序设计', '移动应用开发', '数据库概论', '土木工程概论'],
        crosshair: true
      },
      yAxis: {
        min: 0,
        title: {
          text: '学生人数（人）'
        }
      },
      tooltip: {
        headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
        pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
          '<td style="padding:0"><b>{point.y:.1f} 人</b></td></tr>',
        footerFormat: '</table>',
        shared: true,
        useHTML: true
      },
      plotOptions: {
        column: {
          pointPadding: 0.2,
          borderWidth: 0
        }
      },
      credits: {
        enabled: false
      },
      series: [{
        name: '学生人数',
        data: [64, 58, 61, 49]
      }]
    });

    const Highcharts2 = require('highcharts');
    require('highcharts/modules/exporting')(Highcharts2);
    // 创建图表
    Highcharts2.chart('container2', {
      chart: {
        type: 'pie'
      },
      title: {
        text: '教师学位分布情况'
      },
      subtitle: {
        text: '来源： 教务系统'
      },
      xAxis: {
        categories: [],
        crosshair: true
      },
      yAxis: {
        min: 0,
        title: {
          text: '学生人数（人）'
        }
      },
      tooltip: {
        headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
        pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
          '<td style="padding:0"><b>{point.y:.1f} 人</b></td></tr>',
        footerFormat: '</table>',
        shared: true,
        useHTML: true
      },
      plotOptions: {
        column: {
          pointPadding: 0.2,
          borderWidth: 0
        }
      },
      credits: {
        enabled: false
      },
      series: [{
        name: '教师人数',
        data: [['博士', 59], ['硕士', 34], ['研究生', 52], ['本科生', 64]]
      }]
    });
  }

  // 获取所有Course数据
  getCourses(): void {
    this.courseService.getCourses()
      .subscribe(res => {
        this.courses = res;
        // tslint:disable-next-line:prefer-for-of
        for (let i = 0; i < this.courses.length; i++) {
        this.newCourses.push(this.courses[i].description);
        }
      });
  }

}
